export default function subWords() {
  ElMessageBox.prompt('输入需要被翻译的词条', '添加', {
    confirmButtonText: '确认',
    cancelButtonText: '取消',
    inputPattern: /^[\u4e00-\u9fa5A-Za-z0-9_-]+[\)）（，；\(\.\%。\u4e00-\u9fa5A-Za-z0-9_-]?$/,
    inputErrorMessage: '词条不合法'
  }).then(({
    value
  }) => {
    $.ajax({
      url: "/api/addWords",
      type: "POST",
      timeout: 1000,
      data: {
        word: value
      },
      dataType: "JSON",
      success: function (response) {
        if (response.info == 1) {
          ElMessage.success('添加成功！');
        } else {
          ElMessage.error('添加失败！');
        }
      },
      error: function () {
        ElMessage.error('添加失败！');
      }
    });
  }).catch(() => {
    ElMessage({
      type: 'info',
      message: '取消'
    });
  });
}