export default function submitTranslation(text) {
  // let content = prompt("请输入释义")
  ElMessageBox.prompt('请输入释义', '提交', {
    confirmButtonText: '确认',
    cancelButtonText: '取消',
    inputPattern: /^[\u4e00-\u9fa5A-Za-z0-9_-]+[\)）（，；\(\.\%。\u4e00-\u9fa5A-Za-z0-9_-]?$/,
    inputErrorMessage: '空值或者含有非法字符'
  }).then(({
    value
  }) => {
    // 查询词条
    $.ajax({
      url: "/api/queryWord",
      type: "POST",
      timeout: 1000,
      data: {
        word: text
      },
      dataType: "JSON",
      success: function (response) {
        if (response.info == 1) {
          // 若有则直接追加翻译
          $.ajax({
            url: "/api/submitTranslationsToTemp",
            type: "POST",
            timeout: 1000,
            data: {
              word: text,
              translation: value
            },
            dataType: "JSON",
            success: function (response) {
              console.log(response);
              if (response.info == 1) {
                ElMessage.success('提交成功！');
              } else {
                ElMessage.error('提交错误！');
              }
            },
            error: function () {
              ElMessage.error('提交失败！');
            }
          });
        } else {
          // 若没有则添加词条
          $.ajax({
            url: "/api/addWords",
            type: "POST",
            timeout: 1000,
            data: {
              word: text
            },
            dataType: "JSON",
            success: function (response) {
              if (response.info == 1) {
                // 添加完之后追加翻译
                $.ajax({
                  url: "/api/submitTranslationsToTemp",
                  type: "POST",
                  timeout: 1000,
                  data: {
                    word: text,
                    translation: value
                  },
                  dataType: "JSON",
                  success: function (response) {
                    console.log(response);
                    if (response.info == 1) {
                      ElMessage.success('提交成功！');
                    } else {
                      ElMessage.error('提交错误！');
                    }
                  },
                  error: function () {
                    ElMessage.error('提交失败！');
                  }
                });
              }
            },
            error: function () {
              ElMessage.error('添加词条失败！');
            }
          });
        }
      },
      error: function () {
        ElMessage.error('查询时发生错误！');
      }
    });
  }).catch(() => {
    ElMessage({
      type: 'info',
      message: '取消'
    });
  });

  // if (content == null || content.match(/\S/) == null || !legal.test(content)) {
  //     alert("空值或者含有非法字符！")
  // } else {

  // $.ajax({
  //     url: "/api/submitTranslationsToTemp",
  //     type: "POST",
  //     timeout: 1000,
  //     data: { word: text, translation: content },
  //     dataType: "JSON",
  //     success: function (response) {
  //         console.log(response);
  //         alert("提交成功！")
  //     },
  //     error: function () {
  //         alert("发生错误！")
  //     },
  // });
  // }
}